import React, { Component } from "react";
import { getCetakUlang } from "../../actions/cetakUlangAction";
import { getUUID } from "../../actions/fakturAction";
import { getsupplierList } from "../../actions/supplierAction";
import Axios from "axios";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import notrespond from "../../Images/500.svg";
import Skeleton from "react-loading-skeleton";
import { Link } from "react-router-dom";
import { gagal } from "../../Alert";
import PrintNota from "../../Stocking/Component/PrintNota";
import HeaderCard from "../../HeaderCard";
import FOrmLaporanPindahBarangHead from "./FormLaporanPindahBarangHead";
import { AxiosMasterGet } from "../../Axios";
import { getToday } from "../../getDate";
import { formatGram, formatRp } from "../../helper";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class FormLaporanPindahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "lokasi_asal",
          text: "Lokasi Asal",
        },
        {
          dataField: "lokasi_tujuan",
          text: "Lokasi Tujuan",
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.print(row)}
                >
                  <i className="fa fa-print mr-1"></i>
                  Print
                </button>
              </div>
            );
          },
        },
      ],
      data: [],
      listPenerimaan: [],
    };
  }
  componentDidMount() {
    this.props.dispatch(getsupplierList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getCetakUlang());
  }
  getDetails(faktur) {
    Axios.get(
      serverNonMaster + "item/dc/pindahbarang/detail?noFaktur=" + faktur
    )
      .then((res) => this.setLocal(res.data))
      .catch(() =>
        gagal("Sepertinya ada kesalahan, Mohon Periksa Koneksi anda")
      );
  }
  setLocal(data) {
    localStorage.setItem("pindahBarang", JSON.stringify(data.barangDetails));
    this.print(
      data.tanggal,
      data.kodeLokasiAsal,
      data.kodeLokasiTujuan,
      data.noBon
    );
  }
  print(data) {
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = data;
    // for each ticket pass all its data into an array
    table.detail_barang.forEach((ticket, i) => {
      const ticketData = [
        i++,
        ticket.kode_jenis,
        ticket.nama_jenis,
        ticket.kadar_jual,
        formatGram(ticket.bruto),
        formatGram(ticket.netto, 3),
        formatRp(ticket.ongkos_rp),
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    var columTable = [
      "No",
      "Kode Jenis",
      "Nama Jenis",
      "Kode Tukar",
      "Bruto",
      "Netto",
      "Ongkos (Rp)",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    PrintNota(
      "Tanggal",
      data.tanggal,
      "Lokasi",
      data.lokasi_asal + " - " + data.lokasi_tujuan,
      "Nomor Bon",
      data.no_bon,
      "",
      "",
      "ADMIN",
      getToday(),
      "",
      columTable,
      "PINDAH BARANG",
      tableRows
    );
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  getData(data) {
    AxiosMasterGet(
      `report/pindah-barang/ALL&ALL&${data.tanggalAwal}&${data.tanggalAkhir}`
    )
      .then((res) =>
        this.setState({
          listPenerimaan: res.data,
        })
      )
      .catch(() =>
        gagal("Sepertinya ada gangguan, tidak bisa mengambil data dari server")
      );
  }

  render() {
    return (
      <>
        <div className="container-fluid">
          <ol className="breadcrumb mb-2">
            <Link to="/cetakUlangBukti">
              <button type="button" className="btn btn-secondary">
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="card">
            <HeaderCard title="Cetak Ulang Bukti Pindah Barang" />
            <div className="card-body">
              <br />
              <div className="row">
                <div className="col-lg-12">
                  <FOrmLaporanPindahBarangHead
                    onSubmit={(data) => this.getData(data)}
                  />
                </div>
                <div className="col-lg-12">
                  {this.state.listPenerimaan ? (
                    <ToolkitProvider
                      bootstrap4
                      keyField={
                        this.props.namaKey ? this.props.namaKey : "kode"
                      }
                      data={this.state.listPenerimaan}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="col-lg-12 col-md-12 mt-5">
                          <div className="text-right">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Tuliskan Kode / Nama"
                              style={{ margin: 0 }}
                            />
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            headerClasses="header-table"
                            striped
                            hover
                            bordered={true}
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton
                            {...props.csvProps}
                            className="btn btn-primary"
                          >
                            Export CSV
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="NOT RESPOND"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width="100%" height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default connect()(FormLaporanPindahBarang);
